import React from 'react'
import styles from './intro.module.css'


class Intro extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      prefade: styles.prefade,
    };
  }

  componentDidMount() {
    let prefaders = document.querySelectorAll('[data-fade]');
    [].map.call(prefaders, (el, index) => (
      el.style.transitionDelay = (index+1)*0.2 + 's',
      this.setState({
        prefade: styles.prefade__active
      })
    ))
  }


  render() {

    const myStyle = {
      opacity: 0,
    }

    return (
      <div className={styles.container}>
        <h2 data-fade className={this.state.prefade}>Welcome to KAPP Developments.</h2>
        <div data-fade className={this.state.prefade}>between the lines</div>
        <div data-fade className={this.state.prefade}>between the lines 2</div>
        <div data-fade className={this.state.prefade}>between the lines 3</div>
        <p data-fade className={this.state.prefade}>We are a team of highly skilled tradespeople, passionate about getting the job done to utmost customer satisfaction.<br />
        Check out our services below:</p><br />

      </div>
    )
  }
}

export default Intro



